.blog {
  &__articles {} }

.article {
  @include flex(flex-start, normal, row, wrap);
  margin-bottom: 60px;
  @include tr(box-shadow .3s);
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 15px;
  padding-right: 15px;
  @include hover {
    box-shadow: 0 0 10px rgba($gray, .4); }
  @include xl {
    margin-bottom: 50px; }
  @include lg {
    margin-bottom: 50px; }
  @include md {
    margin-bottom: 40px; }
  @include sm {
    margin-bottom: 25px; }
  @include xs {
    margin-bottom: 15px; }
  &__content {
    width: 50%;
    @include flex(center, normal, column);
    padding-right: 45px;
    opacity: 0;
    @include lg {
      padding-right: 15px; }
    @include sm {
      width: 100%;
      display: block;
      padding-right: 0;
      margin-bottom: 15px; } }
  &__img {
    width: 50%;
    display: block;
    padding-left: 15px;
    overflow: hidden;
    @include sm {
      width: 100%;
      padding-left: 0; } }
  &__title {
    display: block;
    margin-bottom: 20px;
    strong {
      display: block; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    display: block;
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__btn {
    display: block;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__meta {
    display: block;
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } }
  &__user {
    display: block; } }

.meta {
  @include flex(flex-start, center);
  color: $gray-light;
  font-size: 12px;
  &__item {
    @include flex(flex-start, center);
    @include notlast {
      margin-right: 30px; } }
  &__icon {
    font-size: 0;
    display: block;
    margin-right: 5px;
    .icon {
      font-size: 16px; } } }

.user {
  @include flex(flex-start, center);
  &__img {
    @include cover;
    display: block;
    width: 60px;
    height: 60px;
    flex: 0 0 60px;
    @include md {
      width: 40px;
      height: 40px;
      flex: 0 0 40px; } }
  &__text {
    display: block;
    padding-left: 10px;
    color: $gray-light;
    font-size: 10px;
    line-height: 1.4;
    @include reg; } }

.article-img {
  display: block;
  padding-left: 8%;
  padding-bottom: 8%;
  position: relative;
  @include before {
    @include coverdiv;
    z-index: 0;
    top: 15px;
    left: 0;
    width: 100%;
    background-color: $yellow;
    transform: translate(8%, -8%);
    opacity: 0;
    @include tr(opacity .3s, transform .3s); }
  @include sm {
    padding-left: 4%;
    padding-bottom: 4%; }
  &.is-show {
    @include before {
      opacity: 1;
      transform: translate(0, 0); } }
  &__inner {
    display: block; }
  &__img {
    @include cover;
    display: block;
    background-color: $gray;
    position: relative;
    z-index: 1;
    padding-top: 85%;
    opacity: 0;
    @include sm {
      padding-top: 50%; } } }
