.principles {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px; }

.principle {
  padding-left: 10%;
  padding-right: 10px;
  opacity: 0;
  @include sm {
    padding-left: 0; }
  &__wrap {
    width: 33.33333%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px;
      width: 50%; }
    @include xs {
      margin-bottom: 10px; }
    @include xss {
      width: 100%; } }
  &__title {
    margin-bottom: 20px;
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } }
  &__nmb {
    margin-bottom: 20px;
    @include lg {
      margin-bottom: 15px; }
    @include md {
      margin-bottom: 10px; } }
  &__text {
    @include xl {
      br {
        display: none; } } } }
